import React, { Component } from "react";
import CardLayout from "../../components/Cards/CardLayout";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import { FormGroup, Col,} from "reactstrap";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
import { Link } from "react-router-dom";
import Loader from "../../components/Loader/Loader";

class Exceptions extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true
    };
  }
  componentDidMount() {
    this.props.getExceptionList();
    let compRef = this;
    setTimeout(() => {
      compRef.setState({
        loading: false
      });
    }, 2000);
  }

  onDeleteState(cell, row) {
    return (
      <Link to={this} style={{ pointerEvents: "none", opacity: 0.5 }}>
        <i className="fa fa-trash" title="Delete" />
      </Link>
    );
  }

  onEditState(cell, row) {
    return (
      <Link to={this} style={{ pointerEvents: "none", opacity: 0.5 }}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  showRatingStars(cell, row) {
   
  }
  render() {
    const sortingOptions = {
      defaultSortName: "Logdate",
      noDataText:
        "No records found for exceptions",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.exceptionList.length
        }
      ],
      sizePerPage: 5
    };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout name="Exception Report">
        <FormGroup row className="div-padding">
          <Col xs="12">
            <BootstrapTable
              ref="table"
              data={this.props.exceptionList}
              pagination={this.props.exceptionList.length > 0 ? true : false}
              search={this.props.exceptionList.length > 0 ? true : false}
              options={sortingOptions}
              exportCSV={this.props.exceptionList.length > 0 ? true : false}
              hover={true}
              csvFileName="Exception Report.csv"
            >
              <TableHeaderColumn
                dataField="Logid"
                headerAlign="left"
                isKey
                hidden
              >
                Logid
              </TableHeaderColumn>
              <TableHeaderColumn
                dataField="ExceptionType"
                headerAlign="left"
                width="40"
                dataSort={true}
                csvHeader = "Exception Type"
              >
              Exception Type
              </TableHeaderColumn>
              <TableHeaderColumn
                dataField="ExceptionMsg"
                headerAlign="left"
                width="30"
                csvHeader = "Exception Message"
                dataSort={true}
              >
              Exception Message
              </TableHeaderColumn>
              <TableHeaderColumn
                dataField="Logdate"
                headerAlign="left"
                width="20"
                csvHeader = "Date"
              >
                Date
              </TableHeaderColumn>
            </BootstrapTable>
          </Col>
        </FormGroup>
      </CardLayout>
    );
  }
}
const mapStateToProps = state => {
  return {
    exceptionList: state.exceptionReducer.exceptionList
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getExceptionList: () => dispatch(actions.getExceptionList()),
    
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Exceptions);



